<template>
  <div class="footbar">
    <div class="footbar-ico">
      <div class="icon-wrapper"  @click="$toast('功能未实现')">
        <a :href="prop.shopLink" class="footbar-shop-link">shop</a>
        <i class="icon-shop"></i>
        <span>店铺</span>
      </div>
      <div class="icon-wrapper"  @click="$toast('功能未实现')">
        <i class="icon-message"></i>
        <span>客服</span>
      </div>
      <div class="icon-wrapper" @click="changeStar">
        <i class="icon-star" :style="starStyle"></i>
        <span>收藏</span>
      </div>
    </div>
    <div class="footbar-bottom">
      <div class="btn-pink" @click="addToCart">加入购物车</div>
      <div class="btn-red" @click="$toast('功能未实现')">立即购买</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    prop: {
      type: Object
    }
  },
  data() {
    return {
      isStar: false
    }
  },
  computed: {
    starStyle() {
      return this.isStar
        ? { 'background-position-y': -27 + 'px' }
        : { 'background-position-y': 0 + 'px' }
    }
  },
  methods: {
    changeStar() {
      this.isStar ? (this.isStar = false) : (this.isStar = true)
    },
    // 商品添加购物车事件
    async addToCart() {
      await this.$store.dispatch('addGoodsToCart', this.prop.prod)
      // 添加购物车成功
      this.$toast('添加购物车成功')
    }
  }
}
</script>

<style lang='less' scoped>
.footbar {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  height: 49px;
  display: flex;
  background-color: #fff;
  .icon-wrapper {
    position: relative;
    float: left;
    width: 49px;
    height: 49px;
    border-right: 1px solid rgb(237, 237, 237);
    border-top: 1px solid rgb(237, 237, 237);
    padding: 2px 0;
    text-align: center;
    font-size: 14px;
    .icon-shop,
    .icon-message,
    .icon-star {
      display: block;
      height: 22px;
      width: 22px;
      margin: 0 auto;
      background: url('~assets/img/detail/detail_bottom.png');
      background-size: 22px;
      background-repeat: no-repeat;
      margin-top: 5px;
    }

    .icon-shop {
      background-position-y: -99px;
    }
    .icon-message {
      background-position-y: -52px;
    }
    .icon-star {
      transition: all 0.5s;
    }
    .footbar-shop-link {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      font-size: 0;
    }
  }
  .footbar-bottom {
    background-color: pink;
    flex-grow: 3;
    .btn-pink,
    .btn-red {
      float: left;
      width: 50%;
      text-align: center;
      line-height: 49px;
      height: 49px;
    }
    .btn-pink {
      color: var(--color-high-text);
      background-color: #ffe6e8;
    }
    .btn-red {
      color: #fff;
      background: linear-gradient(90deg, #ff5777, #ff468f);
    }
  }
}
</style>
