<template>
  <div class="rate">
    <p class="rate-title">用户评价</p>
    <div v-if="prop">
      <div class="rate-user" v-if="prop.user">
        <img :src="prop.user.avatar" alt="" class="rate-user-avatar" />
        <span class="rate-username">{{ prop.user.uname }}</span>
      </div>
      <p class="rate-content">{{ prop.content }}</p>
      <p class="rate-detailinfo">
        <span>{{ prop.created | rateDate }}</span>
        <span class="rate-detailinfo-style">{{ prop.style }}</span>
      </p>
      <div v-if="prop.images" class="rate-images">
        <img :src="item" alt="" v-for="item in prop.images" :key="item" class="rate-img" />
      </div>
    </div>
    <div style="margin-top: 10px">暂无评价</div>
  </div>
</template>

<script>
export default {
  props: {
    prop: {
      type: [Object, Boolean]
    }
  },
  filters: {
    rateDate: function (value) {
      if (!value) return ''
      const date = new Date(value * 1000)
      const y = date.getFullYear()
      const m = date.getMonth()
      const d = date.getDay()
      return y + '-' + m + '-' + d
    }
  }
}
</script>

<style lang='less' scoped>
.rate {
  padding: 20px 10px 20px 10px;
  border-bottom: 5px solid #eee;
  .rate-title {
    color: #000;
    border-bottom: 2px solid #eee;
    padding-bottom: 15px;
  }
  .rate-user {
    padding: 10px 0;
    .rate-user-avatar {
      width: 40px;
      height: 40px;
      border-radius: 50%;
    }
    .rate-username {
      color: #333;
      margin-left: 10px;
    }
  }
  .rate-content {
    font-size: 14px;
    line-height: 18px;
    padding: 0 0 10px 0;
  }
  .rate-detailinfo {
    font-size: 14px;
    color: #999;
    .rate-detailinfo-style {
      margin-left: 10px;
    }
  }
  .rate-images {
    margin-top: 10px;
    .rate-img {
      width: 70px;
      height: 70px;
    }
  }
  .rate-images img:not(:first-child) {
    margin-left: 10px;
  }
}
</style>
