<template>
  <div class="swiper">
    <img :src="imgLink" alt="" class="swiper-img">
  </div>
</template>

<script>
export default {
  props: {
    prop: {
      type: Array,
      requeir: true
    }
  },
  computed: {
    imgLink() {
      return this.prop[0]
    }
  }
}
</script>

<style lang='less' scoped>
.swiper{
  height:50vh;
  overflow: hidden;
.swiper-img{
  width: 100%;
}}
</style>
