<template>
  <div class="shop-info-box">
    <a :href="prop.shopUrl" class="shopLink">
      <div class="shop-title">
        <div class="shop-logo">
          <img :src="prop.shopLogo" alt="" />
        </div>
        <div class="shop-name">{{ prop.name }}</div>
      </div>
      <div class="shop-info">
        <div class="shop-cate-info">
          <div class="shop-csells">
            <p>{{ prop.cSells | numberFilter }}</p>
            <p class="shop-csells-title">总销量</p>
          </div>

          <div class="shop-cgoods">
            <p>{{ prop.cGoods }}</p>
            <p class="shop-csells-title">全部宝贝</p>
          </div>
        </div>
        <div class="shop-info-line"></div>
        <div class="shop-score" v-if="prop.score">
          <div>
            <p v-for="item in prop.score" :key="item.name" class="shop-score-name">
              {{ item.name }}
            </p>
          </div>
          <div>
            <p
              v-for="item in prop.score"
              :key="item.name"
              class="shop-score-score"
              :style="{ color: item.isBetter ? 'red' : 'green' }"
            >
              {{ item.score }}
            </p>
          </div>
          <div>
            <p
              v-for="item in prop.score"
              :key="item.name"
              class="shop-score-isBetter"
              :style="{ 'background-color': item.isBetter ? 'red' : 'green' }"
            >
              {{ item.isBetter | scoreFilter }}
            </p>
          </div>
        </div>
      </div>
      <div class="link">
        <div class="shop-link-btn">
          <span>进店逛逛</span>
        </div>
        <a :href="prop.allGoodsUrl"> <div class="all-goods-link">全部商品</div></a>
      </div>
    </a>
  </div>
</template>

<script>
export default {
  props: {
    prop: {
      type: Object,
      require: true
    }
  },
  filters: {
    // 将销量转换成万
    numberFilter: function (value) {
      if (!value) return ''
      value = value / 10000
      if (value.isNaN) return ''
      value = value.toString()
      return value.slice(0, -3) + '万'
    },
    // 将服务满意度平均值转换为汉字
    scoreFilter: function (value) {
      if (value) {
        return '高'
      } else {
        return '低'
      }
    }
  }
}
</script>

<style lang='less' scoped>
.shop-info-box {
  padding: 20px 10px 0 10px;
  border-bottom: 5px solid #eee;
  .shop-title {
    height: 50px;
    .shop-logo {
      float: left;
      height: 50px;
      width: 50px;
      border-radius: 50%;
      overflow: hidden;
      border: 1px solid #ccc;
      img {
        width: 100%;
        height: auto;
      }
    }
    .shop-name {
      float: left;
      line-height: 50px;
      margin-left: 15px;
    }
  }
  .shop-info {
    display: flex;
    margin-top: 10px;
    justify-content: center;
    height: 80px;
    align-items: center;
    .shop-info-line {
      height: 40px;
      width: 1px;
      background-color: #ccc;
    }
    .shop-cate-info,
    .shop-score {
      flex: 1;
      text-align: center;
    }
    .shop-cate-info {
      display: flex;
      justify-content: space-around;
      font-size: 16px;
      .shop-csells-title {
        margin-top: 5px;
        font-size: 14px;
        line-height: 14px;
      }
    }
    .shop-score {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      .shop-score-score {
        text-align: left;
        margin-right: 5px;
        margin-top: 5px;
      }
      .shop-score-name {
        margin-right: 5px;
        margin-top: 5px;
      }
      .shop-score-isBetter {
        color: #fff;
        margin-top: 5px;
      }
    }
  }
  .link {
    display: flex;
    justify-content: center;
    align-items: center;
    .shop-link-btn,
    .all-goods-link {
      width: 120px;
      border-radius: 10px;
      background-color: #eee;
      height: 24px;
      line-height: 24px;
      font-size: 14px;
      text-align: center;
      margin: 20px;
    }
  }
}
</style>
