<template>
  <div id="detail" ref="detail">
    <nav-bar class="nav-bar">
      <template v-slot:left>
        <img src="@/assets/img/common/back.svg" alt="" @click="returnSuperior" class="back-img" />
      </template>
      <template v-slot:default>
        <span
          class="nav-item"
          :class="{ 'current-nav-item': navBarCurrentIndex === 0 }"
          @click="changeCurrentNav(0)"
          >商品</span
        >
        <span
          class="nav-item"
          :class="{ 'current-nav-item': navBarCurrentIndex === 1 }"
          @click="changeCurrentNav(1)"
          >参数</span
        >
        <span
          class="nav-item"
          :class="{ 'current-nav-item': navBarCurrentIndex === 2 }"
          @click="changeCurrentNav(2)"
          >评论</span
        >
        <span
          class="nav-item"
          :class="{ 'current-nav-item': navBarCurrentIndex === 3 }"
          @click="changeCurrentNav(3)"
          >推荐</span
        >
      </template>
      <template v-slot:right>
        <div class="nav-bar-right" @click="goCart">
          <img src="@/assets/img/tabbar/shopcart.svg" alt="" />
        </div>
      </template>
    </nav-bar>
    <detail-swiper :prop="swiperImg" ref="detailswiper"></detail-swiper>
    <detail-base-info :prop="baseInfoData"></detail-base-info>
    <detail-shop-info :prop="shopInfoData"></detail-shop-info>
    <detail-info :prop="detailInfo" @loadImage="loadImage"></detail-info>
    <item-params :prop="itemParams" ref="itemparams"></item-params>
    <detail-rate :prop="rate" ref="detailrate"></detail-rate>
    <goods-list
      :prop="recommendList"
      :column="3"
      class="detail-goods-list"
      ref="detailgoodslist"
    ></goods-list>
    <back-top></back-top>
    <detail-footbar :prop="footbarMsg"></detail-footbar>
  </div>
</template>

<script>
import NavBar from '@/components/common/navbar/NavBar.vue'
import GoodsList from '@/components/content/goodsList'
import BackTop from '@/components/content/backTop'

import DetailSwiper from './childrenComponents/DetailSwiper'
import DetailBaseInfo from './childrenComponents/DetailBaseInfo'
import DetailShopInfo from './childrenComponents/DetailShopInfo'
import DetailInfo from './childrenComponents/DetailInfo'
import ItemParams from './childrenComponents/ItemParams'
import DetailRate from './childrenComponents/DetailRate'
import DetailFootbar from './childrenComponents/DetailFootbar'

import { getDetailNet } from '@/network/detail.js'
import { getRecommendNet } from '@/network/recommend.js'

import _ from 'lodash'

export default {
  name: 'detail',
  components: {
    NavBar,
    DetailSwiper,
    DetailBaseInfo,
    DetailShopInfo,
    DetailInfo,
    ItemParams,
    DetailRate,
    GoodsList,
    DetailFootbar,
    BackTop
  },
  data() {
    return {
      iid: null,
      navBarCurrentIndex: 0,
      swiperImg: [],
      baseInfoData: {},
      shopInfoData: {},
      detailInfo: {},
      itemParams: [],
      rate: {},
      recommendList: [],
      clientHeight: 0, // 当前窗口高度
      itemOffsetTop: {
        itemParamsOffsetTop: 0,
        rateOffsetTop: 0,
        detailGoodsListOffsetTop: 0
      },
      footbarMsg: {}
    }
  },
  created() {
    // 获取商品数据
    this.getDetail()
    this.getRecommend()
  },
  beforeRouteEnter(to, from, next) {
    // 解决切换页面下次进入滚动条不在顶部
    // console.log(from)
    document.documentElement.scrollTop = 0
    next()
  },
  beforeRouteUpdate(to, from, next) {
    next()
    this.getDetail()
  },

  methods: {
    // 获取商品数据
    async getDetail() {
      // 获取url中的id数据
      this.iid = this.$route.params.id
      const res = await getDetailNet(this.iid)
      // console.log(res)
      if (res.isAxiosError) {
        return this.$router.push('/error')
      }
      // 数据
      const data = res.result
      // console.log(data)
      // 轮播图数据
      this.swiperImg = data.itemInfo.topImages
      // 基础商品数据
      this.baseInfoData = {
        title: data.itemInfo.title,
        nowPrice: data.itemInfo.price,
        oldPrice: data.itemInfo.oldPrice,
        columns: data.columns,
        discountDesc: data.itemInfo.discountDesc,
        discountBgColor: data.itemInfo.discountBgColor,
        services: data.shopInfo.services
      }
      // 商品店铺信息
      this.shopInfoData = data.shopInfo
      // 商品详情信息
      this.detailInfo = {
        desc: data.detailInfo.desc,
        key: data.detailInfo.detailImage[0].key,
        list: data.detailInfo.detailImage[0].list
      }
      // 商品参数数据
      this.itemParams = data.itemParams.info.set
      if (data.rate.cRate === 0) {
        this.rate = false
      } else {
        this.rate = data.rate.list[0]
      }
      // 底部操作条需要的数据
      this.footbarMsg = {
        // 店铺链接
        shopLink: data.shopInfo.shopUrl,
        // 给购物车传递的数据
        prod: {
          iid: this.iid,
          shop: data.shopInfo.name,
          image: data.itemInfo.topImages,
          title: data.itemInfo.title,
          oldPrice: data.itemInfo.oldPrice,
          price: data.itemInfo.lowNowPrice,
          shopId: data.shopInfo.shopId,
          isActived: true
        }
      }

      // 防止页面被渲染过有缓存将页面返回顶部
      document.documentElement.scrollTop = 0
    },
    // 获取推荐商品列表
    async getRecommend() {
      const res = await getRecommendNet()
      // console.log(res)
      if (res.isAxiosError) return this.$toast('服务器异常请刷新页面')
      this.recommendList = res.data.list
      this.recommendList.map((item) => {
        item.img = item.image
        item.iid = item.item_id
        return item
      })
    },

    // 修改选中的tag时调用 修改高亮的tag并将页面移动到目标位置
    changeCurrentNav(index) {
      // 如果还在滚动 阻止事件触发
      if (this.scrolling) return
      this.navBarCurrentIndex = index
      // const content = {
      //   behavior: 'smooth',
      //   block: 'nearest',
      //   inline: 'nearest'
      // }
      // console.dir(this.$refs.itemparams)
      switch (index) {
        case 0:
          this.scrollToTarget(this.$refs.detailswiper.$el, 44)
          break
        case 1:
          this.scrollToTarget(this.$refs.itemparams.$el, 44)
          break
        case 2:
          this.scrollToTarget(this.$refs.detailrate.$el, 44)
          break
        case 3:
          this.scrollToTarget(this.$refs.detailgoodslist.$el, 54)
          break
      }
      // console.log(this.$refs.detailswiper.$el.offsetTop)
    },
    // 返回按钮
    returnSuperior() {
      this.$router.go(-1)
    },
    // 滚动函数 第一个参数为需要滚动到的元素 第二个参数为需要修正多少位置 默认将元素滚动到页面顶部
    scrollToTarget(target, fix = 0) {
      // 滚动的目标位置
      const scrollY = target.offsetTop - fix
      // 当前页面所处的位置
      const scrollTop = document.documentElement.scrollTop
      // console.log(scrollY, scrollTop)
      // console.log(this.scrolling)
      if (scrollTop > scrollY) {
        let diff = scrollTop - scrollY
        // console.log(diff)
        this.scrolling = setInterval(() => {
          if (diff > 1000) {
            document.documentElement.scrollTop -= 500
            diff -= 500
          } else if (diff > 500) {
            document.documentElement.scrollTop -= 200
            diff -= 200
          } else if (diff > 100) {
            document.documentElement.scrollTop -= 50
            diff -= 50
          } else if (diff > 11) {
            document.documentElement.scrollTop -= 10
            diff -= 10
          } else {
            document.documentElement.scrollTop -= diff
            diff -= diff
            // console.log(diff)
            clearInterval(this.scrolling)
            this.scrolling = undefined
          }
        }, 10)
      } else {
        let diff = scrollY - scrollTop
        if (diff === 0) return
        // console.log(diff)
        this.scrolling = setInterval(() => {
          if (diff > 1000) {
            document.documentElement.scrollTop += 500
            diff -= 500
          } else if (diff > 500) {
            document.documentElement.scrollTop += 200
            diff -= 200
          } else if (diff > 100) {
            document.documentElement.scrollTop += 50
            diff -= 50
          } else if (diff > 11) {
            document.documentElement.scrollTop += 10
            diff -= 10
          } else {
            document.documentElement.scrollTop += diff
            diff -= diff
            // console.log(diff)
            // console.log(this.scrolling)
            clearInterval(this.scrolling)
            this.scrolling = undefined
          }
        }, 10)
      }
    },

    // 图片渲染完成的回调事件
    loadImage() {
      // 获取窗口高度
      if (document.body.clientHeight && document.documentElement.clientHeight) {
        this.clientHeight =
          document.body.clientHeight < document.documentElement.clientHeight
            ? document.body.clientHeight
            : document.documentElement.clientHeight
      } else {
        this.clientHeight =
          document.body.clientHeight > document.documentElement.clientHeight
            ? document.body.clientHeight
            : document.documentElement.clientHeight
      }
      this.itemOffsetTop.itemParamsOffsetTop =
        this.$refs.itemparams.$el.offsetTop - this.clientHeight / 3
      this.itemOffsetTop.rateOffsetTop = this.$refs.detailrate.$el.offsetTop - this.clientHeight / 3
      this.itemOffsetTop.detailGoodsListOffsetTop = this.$refs.detailgoodslist.$el.offsetTop - 44
      // 添加滚动事件监听
      this.$nextTick(() => {
        window.addEventListener(
          'scroll',
          // 滚动事件触发过于频繁添加防抖
          _.debounce(() => {
            // 当前滚动高度
            const nowScrollTop = document.documentElement.scrollTop

            if (
              this.navBarCurrentIndex !== 0 &&
              nowScrollTop < this.itemOffsetTop.itemParamsOffsetTop
            ) {
              // 如果商品参数部分还未滚动到页面的一半，在这之前顶部tab名称都应该显示为第一个标签'商品'
              this.navBarCurrentIndex = 0
            } else if (
              this.navBarCurrentIndex !== 1 &&
              nowScrollTop > this.itemOffsetTop.itemParamsOffsetTop &&
              nowScrollTop < this.itemOffsetTop.rateOffsetTop
            ) {
              // 如果商品参数部分滚动到页面的一半，且商品评价还未滚动到一半则，将顶部tab名称改为参数
              this.navBarCurrentIndex = 1
            } else if (
              this.navBarCurrentIndex !== 2 &&
              nowScrollTop > this.itemOffsetTop.rateOffsetTop &&
              nowScrollTop < this.itemOffsetTop.detailGoodsListOffsetTop
            ) {
              // 如果商品参数部分滚动到页面的一半，且商品评价还未完全滚动到页面中，则将顶部tab名称改为评论
              this.navBarCurrentIndex = 2
            } else if (
              this.navBarCurrentIndex !== 3 &&
              nowScrollTop > this.itemOffsetTop.detailGoodsListOffsetTop
            ) {
              // 剩下情况改为推荐
              this.navBarCurrentIndex = 3
            }
          }, 50)
        )
      })
    },
    // 跳转到购物车页面
    goCart() {
      this.$router.push('/cart')
    }
  }
}
</script>

<style lang='less'>
.nav-bar {
  background-color: #fff;
}
.back-img {
  margin-left: 5px;
}
.nav-item {
  font-size: 14px;
  margin: 0 7px;
}
.current-nav-item {
  color: var(--color-high-text);
}
.detail-goods-list {
  margin-top: 10px;
}
.nav-bar-right {
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 50px;
  img {
    width: 50%;
    height: 50%;
  }
}
</style>
