<template>
  <div class="detailinfo">
    <div class="desc">
      <div class="desc-linetop"></div>
      <div class="desc-text">
        {{ prop.desc }}
      </div>
      <div class="desc-linebottom"></div>
    </div>
    <div>
      <p class="key">{{ prop.key }}</p>
      <img
        :src="item"
        alt=""
        v-for="item in prop.list"
        :key="item"
        class="detailinfo-img"
        @load="load"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    prop: {
      type: Object
    }
  },
  data() {
    return {
      counter: 0,
      imagesLength: 0
    }
  },
  methods: {
    load() {
      if (++this.counter === this.imagesLength) this.$emit('loadImage')
    }
  },
  watch: {
    prop() {
      this.imagesLength = this.prop.list.length
    }
  }
}
</script>

<style lang='less' scoped>
.detailinfo {
  padding-top: 10px;
  border-bottom: 5px solid #eee;
  .desc {
    position: relative;
    padding: 20px;
    .desc-text {
      padding: 15px 0;
      font-size: 14px;
      line-height: 20px;
    }
    .desc-linetop,
    .desc-linebottom {
      position: absolute;
      height: 1px;
      width: 100px;
      background-color: #666;
    }
    .desc-linetop {
      top: 10px;
      left: 10px;
    }
    .desc-linebottom {
      bottom: 10px;
      right: 10px;
    }
    .desc-linetop::before,
    .desc-linebottom::before {
      content: '';
      position: absolute;
      width: 5px;
      height: 5px;
      background-color: #000;
      border-radius: 50%;
    }
    .desc-linetop::before {
      top: -2px;
      left: 0;
    }
    .desc-linebottom::before {
      top: -2px;
      right: 0;
    }
  }
  .key {
    padding: 10px;
  }
  .detailinfo-img {
    padding: 10px 0 0 0;
    width: 100%;
    height: auto;
  }
}
</style>
