<template>
  <div>
    <div class="base">
      <div class="title">{{ prop.title }}</div>
      <div class="price">
        <span class="price-nowPrice">{{ prop.nowPrice }}</span>
        <span class="price-oldPrice" v-if="prop.oldPrice">{{ prop.oldPrice }}</span>
        <span
          class="price-discountDesc"
          :style="{ 'background-color': prop.discountBgColor }"
          v-if="prop.discountDesc"
          >{{ prop.discountDesc }}</span
        >
      </div>
      <div class="other" v-if="prop.columns">
        <span>{{ prop.columns[0] }}</span>
        <span>{{ prop.columns[1] }}</span>
        <span>{{ prop.services[0].name }}</span>
      </div>
    </div>
    <div class="service-content">
      <span v-for="(item, index) in showService" :key="index">
        <i class="service-icon" :style="iconStyle(item.icon)"></i>
        {{ item.name }}
      </span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    prop: {
      type: Object,
      require: true
    }
  },
  computed: {
    showService() {
      let list = []
      if (!this.prop.services) {
        return list
      } else {
        // console.log(this.prop.services)
        list = this.prop.services.filter((item, index) => {
          // console.log(item, index)
          return index > 0 && index <= 3
        })
        // console.log(list)
        return list
      }
    }
  },
  methods: {
    iconStyle(url) {
      return {
        background: `url(${url})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: '12px 12px'
      }
    }
  }
}
</script>

<style lang='less' scoped>
.base {
  padding: 10px 10px;
  .title {
    height: 40px;
    line-height: 20px;
    font-size: 18px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .price {
    margin-top: 10px;
    .price-nowPrice {
      color: var(--color-high-text);
      font-size: 22px;
    }
    .price-oldPrice {
      font-size: 12px;
      text-decoration: line-through;
      margin-left: 5px;
      color: #999;
    }
    .price-discountDesc {
      display: inline-block;
      position: relative;
      top: -5px;
      height: 14px;
      line-height: 14px;
      margin-left: 5px;
      padding: 1px 3px;
      color: #fff;
      font-size: 12px;
      border-radius: 7px;
      background: no-repeat;
    }
  }
  .other {
    margin-top: 20px;
    height: 20px;
    font-size: 12px;
    color: #999;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #eee;
  }
}
.service-content {
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  height: 40px;
  line-height: 40px;
  border-bottom: 4px solid #eee;
  .service-icon {
    display: inline-block;
    width: 12px;
    height: 12px;
  }
}
</style>
